.App {

}

.info-box{
	display:-ms-flexbox;
	display:flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.note,.pitch{
	font-size: 2em;
	margin:10px;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

.pitch-point{
	width:30px;
	height:30px;
	background: black;
	border-radius: 15px;
	position:absolute;
	left: 0;
	top:12.5%;
    -webkit-transition:left 0.1s linear;
    -o-transition:left 0.1s linear;
    transition:left 0.1s linear;
	/* margin-left: -5%; */
}

h1{
	text-align: center;
}

.pitch-bar{
	margin:0 auto;
	width:400px;
	height:40px;
	position:relative;
	background: -webkit-gradient(linear, left top, right top, from(hsl(179, 78%, 50%)), color-stop(hsl(126, 96%, 50%)), to(hsl(179, 78%, 50%)));
	background: -webkit-linear-gradient(left, hsl(179, 78%, 50%), hsl(126, 96%, 50%), hsl(179, 78%, 50%));
	background: -o-linear-gradient(left, hsl(179, 78%, 50%), hsl(126, 96%, 50%), hsl(179, 78%, 50%));
	background: linear-gradient(to right, hsl(179, 78%, 50%), hsl(126, 96%, 50%), hsl(179, 78%, 50%));
	border-radius:20px;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



